.cptDetail {
    width:1400px; margin:0 auto;
}
.titleH1{
    width:100%; margin-top:30px;  display:block;
}
.titleH1 a {
    padding-left:20px; color:rgba(255,255,255,1); display:block; position:relative;
}
.titleH1 a::before{
    content:""; width:14px; height:14px; border-left:2px solid rgba(255,255,255,1); border-top:2px solid rgba(255,255,255,1); display:block; 
    position:absolute; left:0; top:11px; transform:rotate(-45deg);
}

.movieBox {
    width:100%; margin-top:30px; display:flex; 
}
.movieBox .imgBox {
    width:max-content; border:1px solid rgba(255,255,255,0.5);
}
.movieBox .infoBox {
    width:100%; padding-left:30px;
}
.movieBox .infoBox .title {
    width:100%; font-size:40px; color:rgba(255,255,255,1);
}
.movieBox .infoBox .year {
    width:100%; margin-top:30px; font-size:18px; font-style:normal; color:rgba(255,255,255,0.8); display:block;
}
.movieBox .infoBox .genresGroup {
    width:100%; margin-top:10px; display:flex;
}
.movieBox .infoBox .genresGroup .genresTitle{
    width:max-content; font-size:18px; font-style:normal; color:rgba(255,255,255,0.8); white-space: nowrap;
}
.movieBox .infoBox .genresGroup .genresDesc{
    width:100%; margin-left:10px;
}
.movieBox .infoBox .genresGroup .genresList{
    width:100%; display:inline;
}
.movieBox .infoBox .genresGroup .genresItem {
    width:100%; font-size:18px; color:rgba(255,255,255,0.8); display:inline;
}
.movieBox .infoBox .desc {
    width:100%; margin-top:40px; font-size:16px; color:rgba(255,255,255,0.8);
}
.loading {
    color:#fff;
}