.movieItem {
    width:max-content; max-width: 270px; padding: 0 10px;
}
.movieItem .imgBox {
    width: 100%; border:1px solid rgba(255,255,255,0.5);
}
.movieItem .imgBox img {
    width:100%; display:block;
}

.movieItem .title {
    margin-top: 10px; 
}
.movieItem .title a {
    width:100%; height:60px; font-size: 20px; line-height: 1.5; color:#fff; text-overflow: ellipsis; overflow: hidden; display: -webkit-box; 
    -webkit-box-orient: vertical; -webkit-line-clamp: 2;
}
.movieItem .genresGroup {
    margin-top:5px; display: flex; flex-wrap: wrap; justify-content: flex-start; gap:5px
}
.movieItem .genresGroup .genresItem {
    font-size: 12px; color:rgba(255,255,255,0.5);
}
.movieItem .summary {
    width:100%; margin-top:5px; font-size: 14px; color: rgba(255,255,255,0.8); text-overflow: ellipsis; overflow: hidden; display: -webkit-box; 
    -webkit-box-orient: vertical; -webkit-line-clamp: 3;
}